import { Dialog, DialogTitle, Button, ButtonGroup, Accordion, AccordionSummary, Typography, AccordionDetails, Container, DialogContent, DialogContentText, DialogActions, Checkbox, FormControlLabel } from "@material-ui/core";
import ExpandMore from "@material-ui/icons/ExpandMore";
import { CSSProperties, useState } from "react";
import { ControlKeys, ControlCommands } from '../util/constants'
import LensCal from "./CommandInterfaces/LensCal";
interface MenuDialog_Props {
    open: boolean,
    active: number[],
    handleClose: () => void,
    handleCommand: (e: ControlKeys) => void
}

export default function MenuDialog(props: MenuDialog_Props) {
    const { open, handleClose, handleCommand, active } = props
    const [shift, setShift] =useState(false)
    const [focus, setFocus] = useState(false)
    const [zoom, setZoom] = useState(false)
    const [heldActive, setHeldActive]= useState<number[]>([])
    const [alertOpen, setAlertOpen]=useState(false)
    const [command, setCommand]=useState<undefined | ControlKeys>(undefined)
    const buttonStyle: CSSProperties = {
        padding: '20px',
        width: '50px',
        touchAction: 'manipulation'
       
    }
    const buttonGroupStyle: CSSProperties = {
        justifyContent: 'center',
        textAlign: 'center'
        
    }
    const close=()=>{
        setShift(false)
        setZoom(false)
        setFocus(false)
        setHeldActive([])
        handleClose()
    }   
    const processCommand=(cmd:ControlKeys)=>{
        if(active.length===0) return;
        if(active.length<2 || heldActive===active ){
            handleCommand(cmd)
        } else{ 
            setCommand(cmd)
            setAlertOpen(true)
        }
    }
    
    const handleAccept=(accept: boolean, hold: boolean)=>{
        if(!accept){setAlertOpen(false); return}

        if(hold){
            setHeldActive(active)
        }
        setAlertOpen(false)

        if(command)
            handleCommand(command)


    }
    const getButton=(cmd: ControlKeys, text: string)=>{
        return <Button style={buttonStyle} key={text+cmd} onClick={(e)=>processCommand(cmd)}>{text}</Button>
    }
    return (
        <Dialog key='lens_dialog' onClose={close} aria-labelledby="simple-dialog-title" open={open}>
            <DialogTitle style={buttonGroupStyle} key='menu_dialog_title' id="simple-dialog-title">Lens Commands</DialogTitle>
            {active.length>0? <Typography style={{margin:'auto'}}>Currently {active.length} Selected</Typography> :<></>}
            {heldActive}
            <LensCal handleCommand={handleCommand} />
            <Accordion key="a_shift" style={{width:'100%'}} expanded={shift} onChange={()=>setShift(!shift)}>
                <AccordionSummary expandIcon={<ExpandMore/>} aria-controls='shift' id='shift-panel' >
                    <Typography key='t_shift'> Shift</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Container style={{width: '70%', marginLeft: '0', paddingLeft:'0'}}>
            <ButtonGroup style={buttonGroupStyle} key='h_shif' variant="outlined">
            {getButton(ControlCommands.LENS_SHIFT_H_FN, '<<<')}
            {getButton(ControlCommands.LENS_SHIFT_H_NN, '<<')}
            {getButton(ControlCommands.LENS_SHIFT_H_SN, '<')}
                {getButton(ControlCommands.LENS_SHIFT_H_SP, '>')}
                {getButton(ControlCommands.LENS_SHIFT_H_NP, '>>')}
                {getButton(ControlCommands.LENS_SHIFT_H_FP, '>>>')}
            </ButtonGroup>
           
            <br></br>
          
            <ButtonGroup style={buttonGroupStyle} key='v_shift' variant="outlined">
            {getButton(ControlCommands.LENS_SHIFT_V_FN, '^^^')}
            {getButton(ControlCommands.LENS_SHIFT_V_NN, '^^')}
            {getButton(ControlCommands.LENS_SHIFT_V_SN, '^')}
                {getButton(ControlCommands.LENS_SHIFT_V_SP, 'v')}
                {getButton(ControlCommands.LENS_SHIFT_V_NP, 'vv')}
                {getButton(ControlCommands.LENS_SHIFT_V_FP, 'vvv')}
            </ButtonGroup>
            </Container>
            </AccordionDetails>
            </Accordion>
            <br></br>
            <Accordion key="shift" expanded={focus} onChange={()=>setFocus(!focus)}>
                <AccordionSummary expandIcon={<ExpandMore/>} aria-controls='focus' id='focus-panel' >
                    <Typography> Focus</Typography>
                </AccordionSummary>
                <AccordionDetails>
                <Container style={{width: '70%', marginLeft: '0', paddingLeft:'0'}}>
            <ButtonGroup style={buttonGroupStyle} key='foucs' variant="outlined">
            {getButton(ControlCommands.LENS_FOCUS_FN, '<<<\r')}
            {getButton(ControlCommands.LENS_FOCUS_NN, '<<\r')}
            {getButton(ControlCommands.LENS_FOCUS_SN, '<\r')}
            {getButton(ControlCommands.LENS_FOCUS_SP, '>\r')}
            {getButton(ControlCommands.LENS_FOCUS_NP, '>>\r')}
            {getButton(ControlCommands.LENS_FOCUS_FP, '>>>\r')}
            </ButtonGroup>
            </Container>
            </AccordionDetails>
            </Accordion>
            <br></br>
            <Accordion key="zoom" expanded={zoom} onChange={()=>setZoom(!zoom)}>
                <AccordionSummary expandIcon={<ExpandMore/>} aria-controls='zoom' id='zoom-panel' >
                    <Typography> Zoom</Typography>
                </AccordionSummary>
                <AccordionDetails>
                <Container style={{width: '70%', marginLeft: '0', paddingLeft:'0'}}>
            <ButtonGroup style={buttonGroupStyle} key='zoom' variant="outlined">
            {getButton(ControlCommands.LENS_ZOOM_FN, '---')}
            {getButton(ControlCommands.LENS_ZOOM_NN, '--')}
            {getButton(ControlCommands.LENS_ZOOM_SN, '-')}
            {getButton(ControlCommands.LENS_ZOOM_SP, '+')}
            {getButton(ControlCommands.LENS_ZOOM_NP, '++')}
            {getButton(ControlCommands.LENS_ZOOM_FP, '+++')}
            </ButtonGroup>
            </Container>
            </AccordionDetails>
            </Accordion>

        <LensAlert active={active} open={alertOpen} handleAccept={handleAccept} />
        </Dialog>
    );
}
interface lensAlert_Props{
    active: number[],
    open: boolean,
    handleAccept: (accept: boolean, hold: boolean)=>void

}
function LensAlert(props: lensAlert_Props){
    const {open, active, handleAccept}=props
    const [hold, setHold]=useState(true)
    const getPJList=()=>{
        let list = ''
        active.forEach(a=>{
            list+=a+' '
        })
        return list
    }

    const handleClick=(c: boolean)=>{
        handleAccept(c,hold)
    }
    return(
        <Dialog
        open={open}>
            <DialogTitle>Multiple PJ Lens Move</DialogTitle>
            <DialogContent>
                <DialogContentText>Moving Many Lens ({active.length})</DialogContentText>
                <DialogContentText>{getPJList()}</DialogContentText>
                <DialogContentText> Are you Sure?</DialogContentText>
                <FormControlLabel control={<Checkbox checked={hold} onClick={()=>setHold(!hold)}/>} label="Silence Till Change" />
                <DialogActions>
                    <Button variant='outlined' onClick={()=>handleClick(true)}> Yes</Button>
                    <Button variant='outlined' onClick={()=>handleClick(false)}> Cancel</Button>
                </DialogActions>
            </DialogContent>
            </Dialog>
    )

}