
import * as C from '../util/constants'
import { Paper, makeStyles, Table, TableBody, TableCell, TableContainer, TableHead, TableRow,  Grid, useMediaQuery, GridSize } from '@material-ui/core'
import { prefres } from "../prefrences"
import {  useMemo, useState } from "react"
import PJCard from "./PJCard"
import ProgrammerTable from "./ProgrammerTable"

interface Programmer_Props {
    selected: number[],
    active: number[],
    world: number[],
    prefs: prefres,
    status: C.RIG_STATUS,
    pjs: Record<number, C.PJ_OBJ>,
    handleTableSelect: (id: number) => void
}
const useStyles = makeStyles({
    root: {
        flexGrow: 1,
    },
    paper: {
        height: 140,
        width: 100,
    },
    table: {
        minWidth: 300,
        maxHeight: 400
    },
    
})
export default function Programmer(props: Programmer_Props) {
    const { active, world, selected, prefs, pjs, handleTableSelect } = props
    const classes = useStyles()

    const getPJs = useMemo(() => {
        console.groupEnd();
        world.sort()
        let pjList: { pj: C.PJ_OBJ, selected: boolean, active: boolean }[] = []
        world.forEach(s => {
            let pj = pjs[s] ? pjs[s] :
                {
                    id: s,
                    online: 'false',
                    shutter: 'Unknown',
                    power: 'Unknown',
                    lastSeen: -1,
                    error: 'NOT SEEN',
                    lampStatus: 'Unknown',
                    edgeBlending: 'Unknown',
                    edgeBlendingMarker: 'Unknown',
                    testPattren: 'Unknown',
                    backColor: 'Unknown',
                    edgeBlendingLeft: 'Unknown',
                    edgeBlendingLower: 'Unknown',
                    edgeBlendingRight: 'Unknown',
                    edgeBlendingUpper: 'Unknown',
                    hdmiResolution: 'Unknown',
                    hdmiSignalLevel: 'Unknown',
                    hdmiVerticalFrequency: 'Unknown',
                    osdPostion: 'Unknown',
                    inputSignalName_Main: 'Unknown',
                    name: 'Unknown'
                }
            if ((prefs.showOnlySelected && selected.includes(s)) || !prefs.showOnlySelected) {
                pjList.push({ pj, selected: selected.includes(s), active: active.includes(s) })
            }

        })
        return pjList

    }, [world, pjs, prefs, selected, active])

    
    const [spacing, setSpacing] = useState( useMediaQuery('(min-width:600px)')? 2 : 0)
    
    const getCards = () => {
       
        return getPJs.map(pj_tupple => {
            return <Grid key={'g_' + pj_tupple.pj.id} item xs={spacing as GridSize} style={{margin: 'auto'}}>
                <PJCard handlePJCard={handleTableSelect} pj_tupple={pj_tupple} />
            </Grid>
        })
    }

    const getRows = () => {
        return getPJs.map(pj_tupple => {

            return <ProgrammerTable handlePJSelect={handleTableSelect} key={pj_tupple.pj.id+'card'} pj_tupple={pj_tupple} />
        })
    }
    return (
        <>  {prefs.programmerTableView?
            <TableContainer key='pjtablecon' component={Paper}>
                <Table key='pjtablet' className={classes.table} size='small'>
                    <TableHead key='pjtablehead'>
                        <TableRow key='pjtableheadrow'>
                            <TableCell key='pjtable_name'>Name</TableCell>
                            <TableCell key='pjtable_id'>ID</TableCell>
                            <TableCell key='pjtable_shutter'>Shutter</TableCell>
                            <TableCell key='pjtable_blend'>Blend</TableCell>
                            <TableCell key='pjtable_mark'>Blend Marker</TableCell>
                            <TableCell key='pjtable_test'>Test Pattren</TableCell>
                            <TableCell key='pjtable_lamp'>Lamp</TableCell>
                            <TableCell key='pjtable_hdmi'>HDMI</TableCell>
                            <TableCell key='pjtable_osd'>OSD</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {getRows()}
                    </TableBody>
                </Table>
            </TableContainer>
        :

            <Grid key='programmer' className={classes.root} spacing={2} container>
                {getCards()}
            </Grid>
}
        </>
    )
}
